import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const tutorialsList = [
	{
		title: 'Web Audio API Intro Tutorial'
	},
	{
		title: 'Learn how to use Git for Deployment in 5 minutes'
	},
	{
		title: 'Javascript Regex Code To Make Readable URLs'
	},
]

const Tutorials = () => (
  <Layout>
    <SEO title="Jeremy Bernier - Tutorials" />
    <p>Tutorials Here</p>
    <ul>
    	{tutorialsList.map(post => {
    		return (
    			<li>{post.title}</li>
  			)
    	})}
    </ul>
  </Layout>
)

export default Tutorials
